.candidate_card{
    display:flex;
    flex-direction: row;
    padding: 0.5rem;
    align-items: center;
    justify-content: space-evenly;
    width: 42%;
    background-color: white;
    border-radius: 0.3rem;
    margin: 0.2rem;
    transition: 1s cubic-bezier(0.04, 1.41, 0.71,-0.22);
    cursor: pointer;
    flex-wrap: nowrap;
    max-height: 6rem;
    position: relative;
    color: rgba(5, 25, 42, 0.78);
}



   
.candidate_card:hover {
 background-color: #3ec78e;
 color: white;
 fill: white;
}


.candidate_card .avatar{
    color: #3ec78e;
}

.candidate_card .texts{
 display: flex;
 flex-wrap: nowrap;
 flex-direction: column;

}


   .candidate_card .texts span{
    width: 100%;
    font-size: 0.65rem;
    margin: 0.2rem;
    
   }

   .candidate_card .texts span:nth-child(1){
    font-weight: 900;
    color: rgb(5, 25, 42);
    display: flex;
    justify-content: space-between;
    width: 100%;
   }

   .candidate_card .texts span:nth-child(1) .candidate_status_progress{
    height: 0.5rem;
    width: 0.5rem;
    background-color: yellow;
    clip-path: circle(50%);
   }

   .candidate_card .texts span:nth-child(1) .candidate_status_reject{
    height: 0.5rem;
    width: 0.5rem;
    background-color: rgb(255, 51, 51);
    clip-path: circle(50%);
   }

   .candidate_card .texts span:nth-child(1) .candidate_status_success{
    height: 0.5rem;
    width: 0.5rem;
    background-color: #3e98c7;
    clip-path: circle(50%);
   }

   .candidate_card .texts span:nth-child(1) .candidate_status_new{
    height: 0.5rem;
    width: 0.5rem;
    background-color: #cfcfcf;
    clip-path: circle(50%);
   }

   .candidate_card .texts span:nth-child(2){
    font-weight: 500;
   }

   .candidate_card .texts span:nth-child(3){
    font-weight: 400;
   }