.gradient{
    width: 100%;
    background: linear-gradient(to left,rgb(224, 25, 92), #3ec78e, rgb(251, 238, 48),rgb(53, 100, 255));
    padding: 0.1rem;
    margin: 0;
    animation: roll 1s linear  ;
    position: fixed;
    z-index: 99999;
    top: 0rem
    
}

@keyframes roll{
    0%{
        filter: hue-rotate(0);
        width: 0%;
    }
    100%{
        filter: hue-rotate(360deg);
        width: 100%;
    }
}

header {
   
    display: flex;
    width: 100%;
    background-color: #3ec78e;
    padding: 1rem;
    justify-content: space-between;
    flex-direction: row;
    position: fixed;
    z-index: 99999;
    top: 0.2rem;
}

header ul {
    display: flex;
    flex-direction: row;
    list-style: none;
}

header ul li {
    margin-right: 2em;
}

header ul li a{
    color: white;
    text-decoration: none;
    font-size: 0.65rem;
}

header ul li:nth-child(1){
    font-weight: 700;
}

header ul li:nth-child(1) a:hover{
    text-decoration: none;
}



header ul li a:hover{
    color: white;
    text-decoration: underline;
}



