.job_container{
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    margin-top: 4rem;
}

.job_container .button_container{
    padding: 1rem;
}

.job_container .button_container button{
    position: absolute;
    top:5rem;
    left:4%;
    padding: 0.75rem;
    background-color: white;
    font-size: 0.85rem;
    border: none;
    border-radius: 0.1rem;
    box-shadow: 0.5px 0.5px 10px rgb(179, 179, 179);
    color: rgb(5, 25, 42);

}

.job_container .button_container button:hover{
    box-shadow: 0.5px 0.5px 1px rgb(179, 179, 179);
}

.job_container .button_container button:focus{
    box-shadow: 0.5px 0.5px 1px rgb(179, 179, 179);
    background-color: #3ec78e;
    color: white;
    outline: none;
}

.job_container  .emphasis{
    font-size: 1.5rem;
    width: 100%;
    color: rgb(5, 25, 42);
    font-weight: 700;
    text-align: center;

}

.job_container .content{
    margin-top: 10%;
    display: flex;
    align-items: center;
    flex-direction: row;
    padding: 1rem;
    justify-content: center;
    flex-wrap: wrap;
}

.job_container .content .notifications{

    display: flex;
    
}


.job_container .content form{
    top: 10rem;
    padding: 1rem;
    display: none;
    position: absolute;
    width: 90%;
    background-color: white;
    flex-direction: column;
    animation: fade 1s;
    flex-wrap: wrap;
    align-items: center;
    
}

.job_container .content form .layout{
    padding: 1rem;
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    justify-content: space-between;
    width: 80%;
}

.job_container .content form .form_contain{
   
    display: flex;
    background-color: white;
    flex-direction: column;
}

.job_container .content form .form_contain div{
    margin-top: 2rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
    
}

.job_container .content form .form_contain div label{
    font-size: 0.85rem;
    margin: 0rem 1rem;
    color:rgb(5, 25, 42);
}

.job_container .content form .form_contain div  input[type='text']{
    font-size: 0.85rem;
    padding: 0.25rem;
    margin-left: 0.5rem;
    border: solid 0.02rem #3ec78e;
    background-color: transparent;
    color:rgb(5, 25, 42);
}

.job_container .content form .form_contain div  input[type='number']{
    font-size: 0.85rem;
    padding: 0.25rem;
    margin-left: 0.5rem;
    border: solid 0.02rem #3ec78e;
    background-color: transparent;
    color:rgb(5, 25, 42);
}



.job_container .content form .form_contain div  textarea{
    font-size: 0.85rem;
    padding: 0.25rem;
    margin-left: 0.5rem;
    width: 13rem;
    height: 10rem;
    border: solid 0.02rem #3ec78e;
    background-color: transparent;
    color:rgb(5, 25, 42);
}

.job_container .content form .form_contain div  select{
    font-size: 0.85rem;
    padding: 0.25rem;
    margin-left: 0.5rem;
    border: solid 0.02rem #3ec78e;
    background-color: transparent;
    color:rgb(5, 25, 42);
    width: 13rem;
}



@keyframes fade {
    0% {opacity: 0;}
    25% {opacity:0.25 ;}
    50% {opacity:0.5 ;}
    100% {opacity:1 ;}
}


.job_container .content form .button_actions{
    display: flex;
    margin-top: 1.5rem;
}


.job_container .content form .button_actions button{
    padding: 1rem;
    background-color: #3ec78e;
    font-size: 0.85rem;
    border: none;
    border-radius: 0.1rem;
    box-shadow: 0.5px 0.5px 10px rgb(138, 136, 136);
    color: white;
    margin: 1rem;
}

.job_container .content form .button_actions button:nth-child(2){
   background-color: rgb(255, 51, 51);
   
}

.job_container .content form .button_actions button:hover{
box-shadow: 0.5px 0.5px 1px rgb(179, 179, 179);
}