.large_card_container{
    background-color: white;
    border: solid 0.01rem rgba(195, 195, 195, 0.767);
    padding: 0.5rem;
    font-size: 0.75rem;
    width:13rem;
    margin:0.5rem;
    transition: 1s ;
    border-radius: 0.2rem;
}

.large_card_container:hover{
    width: 13.5rem;
}

.large_card_container ul{
    list-style: none;
}

.large_card_container ul li{
    padding: 0.2rem;
    position: relative;
    color: rgb(5, 25, 42);
}

.large_card_container ul li:nth-child(1){
    color: #3ec78e;
    font-weight: 700;
}

.large_card_container ul li:nth-child(2) span{
    color: rgb(5, 25, 42);
    font-weight: 900;
}

.large_card_container ul li b{
    position: absolute;
    right: 0;
    cursor: pointer;
    transition: 1s;
}

.large_card_container ul li b:hover{
    transform: rotateZ(15deg);
}