.emphasis_candidate{
    font-size: 1.5rem;
    width: 100%;
    color: rgb(5, 25, 42);
    font-weight: 700;
    text-align: center;
}

#column{
    margin-top: 2rem;
    display: flex;
    justify-content: space-between;
    color: rgb(5, 25, 42);
    margin-bottom: 5rem;
}




#column #left_side{
    width: 50%;
    display: flex;
    flex-wrap: wrap;
    padding-left: 0.5rem;
    max-height: 1rem;
}

#column #right_side{
    margin-right: 1rem;
    margin-left: 1rem;
    width: 50%;
    background-color: white;
    border-radius: 0.5rem;
    position: relative;
    display: none;
    animation:fade 1s;
}



#right_side .candidate_head{
    width: 100%;
    background-color: #3ec78e;
    border-radius: 0.5rem 0.5rem 0rem 0rem;
    padding: 0.5rem;
}

#right_side .candidate_head ul{
    display: flex;
    width: 100%;
    align-items: center;
    justify-content: space-between;
    list-style: none;
    font-size: 0.75rem;
}

#right_side .candidate_head ul li:nth-child(1){
    color: white;
    font-weight: 900;
}


#right_side .candidate_head ul li:nth-child(2){
    display: flex;

}

#right_side .candidate_head ul li:nth-child(2) div{
    margin: 0rem 0.5rem;
    font-weight: 500;
    transition: 0.5s;
    cursor: pointer;
    border-radius: 0.2rem;
    
}



#right_side .candidate_head ul li:nth-child(2) div:nth-child(1){
    padding: 0.5rem;
    background-color: #ffffff;
}

#right_side .candidate_head ul li:nth-child(2) div:nth-child(2){
    padding: 0.5rem;
    background-color: rgb(255, 51, 51);
    color: #ffffff;
}

#right_side .candidate_head ul li:nth-child(2) div:hover{
     transform: rotateZ(5deg);
}




#column_head{

    display: flex;
    justify-content: space-between;
    margin: 4rem 0.5rem 0rem 0.5rem;
    font-size: 0.85rem;
    
}




#column_head #left_side{
    width: 41%;
    padding: 0.5rem ;
    display: flex;
    flex-wrap: no-wrap;
    padding-left: 0.5rem;
    background-color: white;
    align-items: center;
    box-shadow: 0.01rem 0.01rem 0rem  0.03rem #b3b3b3;
}

#column_head #right_side{
    margin-right: 1rem;
    padding: 0.5rem ;
    width: 50%;
    border-bottom: solid 0.1rem rgba(5, 25, 42, 0.78);
    
}

#column_head #right_side span{
    margin-right: 1rem;
    font-weight: 500;
    color: rgba(5, 25, 42, 0.78);
    font-size: 0.95rem;
}

#column_head #left_side span{
    margin-right: 0.5rem;
    font-weight: 500;
    color: rgba(5, 25, 42, 0.78);
    font-size: 0.75rem;
}

#column_head #left_side select{
    font-size: 0.75rem;
    border: transparent 0.01rem;
    color: #3e98c7;
    font-weight: 500;
    background-color: transparent;
    outline: none;
    width: fit-content;
}

#column_head #left_side select:nth-child(3){
color:rgba(5, 25, 42, 0.78);
border-left: solid 0.001rem rgba(5, 25, 42, 0.479);
}


#column_head #left_side select:focus{
    outline: none;
}

#right_side #candidate_card_detail{
    padding: 1rem;
    margin: 1rem 0rem;
    display: flex;
    border-top: 0.5rem  solid whitesmoke;
    font-size: 0.85rem;
    font-weight: 500;
    color: rgb(5, 25, 42);
}

#right_side #candidate_card_detail span{
    color: rgba(5, 25, 42, 0.78);
    font-size: 0.65rem;
}

#right_side #candidate_card_detail span:nth-child(1){
    width: 6rem;
}

#right_side #candidate_card_detail span:nth-child(2){
    font-weight: 400;
}

#right_side #candidate_card_detail:nth-child(4) span:nth-child(2){
    color: #3e98c7;
}

#right_side #candidate_card_detail:nth-child(5) span:nth-child(2){
    color: #3e98c7;
    cursor: pointer;
}

#right_side #candidate_card_detail:nth-child(7) span:nth-child(2){
    color: #3e98c7;
    cursor: pointer;
}

#right_side #candidate_card_detail:nth-child(8) span:nth-child(2){
    color: #3e98c7;
    cursor: pointer;
}

.empty{
    height: 3rem;
    width: 80%;
    font-size: 0.85rem;
    text-align: center;
}

.empty_detail{
    height: 3rem;
    width: 50%;
    font-size: 0.85rem;
    text-align: center;
    display: block;
    margin-right: 1rem ;
}