.login_container{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    min-height: 100vh;
    color: rgb(5, 25, 42);
    margin-top: 2rem;
}

.login_container h1{
margin-bottom: 3rem;
text-align: center;
font-size: 1rem;
}

.login_container div{
    display: flex;
    align-items: center;
    flex-direction: column;
}

.login_container div h5{
    font-size: 1.5rem;
}

.login_container div h6{
    font-size: 0.75rem;
    font-weight: 400;
