    background-color: transparent;
    width: 100%;
    outline: none;
}





.login_container form button{
    margin: 1rem 0rem;
    padding: 0.8rem;
    background-color: #3ec78e;
    color: white;
    border-radius: 0.1rem;
    box-shadow: none;
