    outline: none;
}

.login_container form button:hover{
    background-color: #3e98c7;
    transition: 1s;
}




.gradient{
    width: 100%;
    background: linear-gradient(to left,rgb(224, 25, 92), #3ec78e, rgb(251, 238, 48),rgb(53, 100, 255));
    padding: 0.1rem;
    margin: 0;
    -webkit-animation: roll 1s linear  ;
            animation: roll 1s linear  ;
    position: fixed;
    z-index: 99999;
    top: 0rem
    
}

@-webkit-keyframes roll{
    0%{
        -webkit-filter: hue-rotate(0);
                filter: hue-rotate(0);
        width: 0%;
    }
    100%{
        -webkit-filter: hue-rotate(360deg);
                filter: hue-rotate(360deg);
        width: 100%;
    }
}

@keyframes roll{
    0%{
        -webkit-filter: hue-rotate(0);
                filter: hue-rotate(0);
        width: 0%;
    }
    100%{
        -webkit-filter: hue-rotate(360deg);
                filter: hue-rotate(360deg);
        width: 100%;
    }
}

