.profile_container {
  width: 100%;
  min-height: 100vh;
  display: flex;
  position: relative;
}

.profile_container .left_content {
  width: 30%;
  background: linear-gradient(to bottom right, #3ec78e 50%, #48c792);
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  margin-top: 1rem;
  color: white;
  position: relative;
}

.profile_container .right_content {
  width: 70%;
  min-height: 100vh;
  display: flex;
  margin-top: 1rem;
  position: relative;
}

.profile_container .left_content .content {
  width: 100%;
  margin-top: 7.5rem;
  display: flex;
  flex-direction: column;
  text-align: center;
  position: relative;
}

.content h6 {
  font-size: 1.5rem;
  font-weight: 200;
  text-align: center;
}

.content .profile_x {
  margin-left: 30%;
  margin-top: 2.5rem;
  display: flex;
  width: 70%;
  justify-content: space-between;
  align-items: center;
}

.content .profile_x .profile_img {
  width: 3.2rem;
  height: 3.2rem;
}

.content .profile_x .profile_img img {
  width: 3rem;
  height: 3rem;
  border-radius: 1.5rem;
  border: solid 0.001rem white;
}

.content .profile_x .profile_actions {
  display: flex;
  flex-direction: column;
  margin-left: 0.8rem;
}

.content .profile_x .profile_actions span {
  font-size: 0.5rem;
  margin-bottom: 0.5rem;
  width: 70%;
}

.content .profile_x .profile_actions input::-webkit-file-upload-button {
  content: "Browse";
  border: none;
  padding: 0.5rem;
  color: rgb(5, 25, 42);
  font-weight: 500;
}

.content .profile_x .profile_actions input[type="file"] {
  border: solid 0.01rem white;
  border-radius: 0.1rem;
  width: 45%;
  font-size: 0.65rem;
  outline: none;
  transition: 1s cubic-bezier(1, 0, 0, 1);
}

.content .profile_x .profile_actions input[type="file"]:active {
  outline: none;
}

.content .profile_x .profile_actions input[type="file"]:hover {
  width: 70%;
}

.content .action_navigation {
  margin-left: 30%;
  width: 70%;
  margin-top: 3.5rem;
  display: flex;
  flex-direction: column;
}

.content .action_navigation ul {
  display: flex;
  width: 100%;
  justify-content: space-between;
  align-items: center;
  flex-direction: column;
  list-style: none;
}

.content .action_navigation ul li {
  cursor: pointer;
  display: flex;
  flex-direction: column;
  padding: 1rem;
  border-bottom: solid 0.01rem rgba(255, 255, 255, 0.198);
  width: 100%;
}

.content .action_navigation ul li span:nth-child(1) {
  font-size: 0.85rem;
  font-weight: 700;
}

.content .action_navigation ul li span:nth-child(2) {
  font-size: 0.5rem;
  font-weight: 500;
}

.content .action_navigation ul .active {
  color: rgb(5, 25, 42);
  background-color: #e7e7e7;
  border: none;
}

.profile_container .right_content .general_form_container {
  width: 100%;
  margin-top: 7.5rem;
  display: flex;
  flex-direction: column;
  text-align: center;
  position: relative;
}
.profile_container .right_content .general_form_container h6 {
  color: rgba(5, 25, 42, 0.732);
  font-weight: 700;
  font-size: 1.5rem;
}

.profile_container .right_content .general_form_container form {
  display: flex;
  flex-direction: column;
  width: 100%;
  padding: 2rem;
  align-items: center;
}

.profile_container .right_content .general_form_container form input {
  width: 70%;
  padding: 0.5rem;
  margin-bottom: 1rem;
  background: white;
  border: transparent;
  color: rgb(5, 25, 42);
  border-radius: 0.1rem;
  font-size: 0.75rem;
}

.profile_container .right_content .general_form_container form input:focus {
  outline: none;
}

.profile_container .right_content .general_form_container form .verify_status {
  width: 70%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-size: 0.75rem;
  padding: 0.5rem;
  margin-bottom: 1rem;
  background: white;
  border: transparent;
  color: rgb(5, 25, 42);
  border-radius: 0.1rem;
}

.profile_container
  .right_content
  .general_form_container
  form
  .verify_status
  span:nth-child(1) {
  font-weight: 700;
}

.profile_container
  .right_content
  .general_form_container
  form
  .verify_status
  span:nth-child(2) {
  font-weight: 700;
  color: #3ec78e;
}

.profile_container .right_content .general_form_container form button {
  background-color: #3ec78e;
  padding: 0.6rem;
  width: 70%;
  border-radius: 1rem;
  border: none;
  color: white;
  font-weight: 700;
  box-shadow: 0.2rem 0.5rem 1rem #3ec78eb7;
  transition: 0.1s linear;
}

.profile_container .right_content .general_form_container form button:focus {
  outline: none;
  background-color: #3e98c7;
}

.profile_container .right_content .general_form_container form .input_flex {
  width: 70%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-size: 0.75rem;
  border: transparent;
  color: rgb(5, 25, 42);
  border-radius: 0.1rem;
}

.profile_container
  .right_content
  .general_form_container
  form
  .input_flex
  input {
  width: 48%;
}

.profile_container .right_content .general_form_container form textarea {
  width: 70%;
  padding: 0.5rem;
  height: 5rem;
  margin-bottom: 1rem;
  background: white;
  border: transparent;
  color: rgb(5, 25, 42);
  border-radius: 0.1rem;
  font-size: 0.75rem;
  resize: none;
}

.profile_container .right_content .general_form_container form textarea:focus {
  outline: none;
}

.profile_container
  .right_content
  .general_form_container
  form
  textarea::-webkit-scrollbar {
  width: 1rem;
}

.profile_container
  .right_content
  .general_form_container
  form
  textarea::-webkit-scrollbar-track {
  outline: 1rem solid white;
}

.profile_container
  .right_content
  .general_form_container
  form
  textarea::-webkit-scrollbar-track-piece {
  outline: 0.8rem solid #3ec78e;
}
