.login_container{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    min-height: 100vh;
    color: rgb(5, 25, 42);
    margin-top: 2rem;
}

.login_container h1{
margin-bottom: 3rem;
text-align: center;
font-size: 1rem;
}

.login_container div{
    display: flex;
    align-items: center;
    flex-direction: column;
}

.login_container div h5{
    font-size: 1.5rem;
}

.login_container div h6{
    font-size: 0.75rem;
    font-weight: 400;
    margin-bottom: 1.5rem;
}

.login_container div h6 span{
   color: #3ec78e;
}

.login_container div h6 span a{
    color: #3e98c7;
    text-decoration: none;
 }

.login_container div h6 span:hover{
    color: #3e98c7;
    transition: 1s;
}

.login_container form{
    display: flex;
    flex-direction: column;
    padding: 2rem;
    background-color: white;
    border-radius: 0.5rem;
    color: rgb(5, 25, 42);
    width: 50%;
}

.login_container form label{
    margin-top: 1rem;
    margin-bottom: 0.5rem;
    font-weight: 500;
    font-size: 0.85rem;
}

.login_container form input{
    padding: 0.5rem;
    border-style:outset;
    border-color: rgba(5, 25, 42, 0.157);
    border-width: 0.01rem;
    border-radius: 0.1rem;
    box-shadow: none;
    background-color: transparent;
    width: 100%;
    outline: none;
}





.login_container form button{
    margin: 1rem 0rem;
    padding: 0.8rem;
    background-color: #3ec78e;
    color: white;
    border-radius: 0.1rem;
    box-shadow: none;
    border: none;
    outline: none;
}

.login_container form button:hover{
    background-color: #3e98c7;
    transition: 1s;
}



